import React, { useState, useEffect } from 'react'
import { withRouter } from 'react-router'
import withTranslation from 'Shared/hoc/withTranslations'
import Modal from '../CareerPro/Components/Modal/Modal'
import { MODAL_DATA, PAYMENT_STATUS_TYPE } from './PlanModalData'
import appRegistry from '../../services/AppRegistry'
import { getLocalData, removeLocalData } from 'Shared/helpers/session'

const PlansModalStatus = (props) => {
  const UrlSearch = new URLSearchParams(props.history.location.search)
  const paymentStatus = UrlSearch.get('redirect')
  const hasToken = appRegistry.storage.get('olive-app-token')
  const CHECK_PAYMENT_STATUS = getLocalData('PAYMENT___STATUS')

  const getUserAndPaymentStatus = () => {
    if (!hasToken && PAYMENT_STATUS_TYPE[paymentStatus] === 'success') return 'success_nonlogin_user'
    if (hasToken && PAYMENT_STATUS_TYPE[paymentStatus] === 'success') return 'success_login_user'
    if (PAYMENT_STATUS_TYPE[paymentStatus] === 'fail') return 'failed'
  }

  console.log('CHECK_PAYMENT_STATUS.........', CHECK_PAYMENT_STATUS)

  const [modalIsOpen, setModalIsOpen] = useState(false)
  const [modalStatus, setModalStatus] = useState(null)
  const closeModal = () => {
    props.history.replace({ search: '' })
    setModalIsOpen(false)
    setModalStatus(null)
  }

  useEffect(() => {
    (!modalIsOpen && !!PAYMENT_STATUS_TYPE[paymentStatus]) && setModalIsOpen(true)
    // (!modalIsOpen && !!PAYMENT_STATUS_TYPE[CHECK_PAYMENT_STATUS]) && setModalIsOpen(true)
  }, [UrlSearch])

  useEffect(() => {
    if (CHECK_PAYMENT_STATUS == 'failed') {
      setModalStatus(false)
    }
    if (CHECK_PAYMENT_STATUS == 'succeeded') {
      setModalStatus(true)
    }
    setTimeout(() => {
      removeLocalData('PAYMENT___STATUS')
    }, 6000)
  }, [])

  return (

    <div>
      { (modalIsOpen && paymentStatus) &&
        <Modal
          title=' '
          modalIsOpen={modalIsOpen}
          setIsOpen={setModalIsOpen}
          closeModal={closeModal}
          // handleSubmit={MODAL_DATA[getUserAndPaymentStatus()].handleSubmit}
          submitText={MODAL_DATA[getUserAndPaymentStatus()].submitText}
          cancelText={MODAL_DATA[getUserAndPaymentStatus()].cancelText}
          // showSubmit={MODAL_DATA[getUserAndPaymentStatus()].showSubmit}
          showCancel={MODAL_DATA[getUserAndPaymentStatus()].showCancel}
          status={MODAL_DATA[getUserAndPaymentStatus()].status}
          heading={MODAL_DATA[getUserAndPaymentStatus()].heading}
          des={MODAL_DATA[getUserAndPaymentStatus()].des}
        />
      }

      {modalStatus && <Modal
        title=' '
        modalIsOpen={closeModal}
        setIsOpen={closeModal}
        closeModal={closeModal}
        // handleSubmit={MODAL_DATA[getUserAndPaymentStatus()].handleSubmit}
        submitText={'submit'}
        cancelText={'cancel'}
        // showSubmit={MODAL_DATA[getUserAndPaymentStatus()].showSubmit}
        showCancel
        status={'fail'}
        heading={'SUCCESS'}
        des={'des'}
      />}

    </div>
  )
}

export default withRouter(withTranslation(PlansModalStatus))



